 background: transparent;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    padding: 7px 12px;\n    margin-left: 20px;\n}\n.page-number[data-v-53939270] {\n    padding: 8px 12px;\n    color: #616161;\n    font-size: 14px;\n    line-height: 16px;\n    background: none;\n    border: none\n}\n.page-number.active[data-v-53939270] {\n    background: #151233;\n    border-radius: 4px;\n    color: #FFFFFF;\n}\n.change-page-number[data-v-53939270] {\n    border: none;\n    background: none;\n}\n.change-page-number i[data-v-53939270] {\n    color: #151233; \n    font-size: 20px; \n    line-height: 20px;\n}\n\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/backoffice/application/applicationList.vue"],"names":[],"mappings":";AAsKA;IACI,qBAAqB;AACzB;AACA;IACI,gBAAgB;IAChB,eAAe;IACf,iBAAiB;IACjB,cAAc;AAClB;AACA;IACI,cAAc;IACd,aAAa;IACb,mBAAmB;AACvB;AACA;IACI,uBAAuB;IACvB,oCAAoC;IACpC,6BAA6B;IAC7B,YAAY;IACZ,aAAa;IACb,mBAAmB;AACvB;AACA;IACI,uBAAuB;IACvB,YAAY;AAChB;AACA;IACI,aAAa;AACjB;AACA;IACI,cAAc;IACd,eAAe;IACf,gBAAgB;AACpB;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,cAAc;IACd,gBAAgB;IAChB,eAAe;IACf,iBAAiB;AACrB;AACA;IACI,mBAAmB;IACnB,yBAAyB;IACzB,8CAA8C;IAC9C,mBAAmB;IACnB,WAAW;AACf;AACA;IACI,iCAAiC;AACrC;AACA;IACI,yBAAyB;AAC7B;AACA;;IAEI,gCAAgC;IAChC;AACJ;AACA;IACI,gCAAgC;IAChC,kBAAkB;IAClB,cAAc;IACd,eAAe;IACf,iBAAiB;AACrB;AACA;IACI,mBAAmB;IACnB,cAAc;IACd,gBAAgB;IAChB,eAAe;IACf,iBAAiB;AACrB;AACA;IACI,mBAAmB;IACnB,yBAAyB;IACzB,kBAAkB;IAClB,WAAW;IACX,iBAAiB;IACjB;AACJ;AACA;IACI,YAAY;IACZ,uBAAuB;IACvB,yBAAyB;IACzB,kBAAkB;IAClB,iBAAiB;IACjB,iBAAiB;AACrB;AACA;IACI,iBAAiB;IACjB,cAAc;IACd,eAAe;IACf,iBAAiB;IACjB,gBAAgB;IAChB;AACJ;AACA;IACI,mBAAmB;IACnB,kBAAkB;IAClB,cAAc;AAClB;AACA;IACI,YAAY;IACZ,gBAAgB;AACpB;AACA;IACI,cAAc;IACd,eAAe;IACf,iBAAiB;AACrB","sourcesContent":["<script setup lang=\"ts\">\n    import { computed,onBeforeMount, reactive, ref, watch, watchEffect } from \"vue\";\n    import { container } from \"../../../../../domain/core/diContainer\";\n    import { IRouter } from \"../../../../../domain/presentation/IRouter\";\n    import { IApplication } from \"../../../../../../../common/qCloudTemp/quickCloud\";\n    import { IBackofficeUseCase } from \"../../../../../domain/useCase/IBackofficeUseCase\";\n    import { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\n    import CustomSelect from \"../../customSelect.vue\"\n\n    interface IApplicationData extends IApplication {\n        organizationId?: string;\n        organizationName?: string;\n    }\n    interface IApplicationListProps {\n        apps: Array<IApplicationData>;\n        displayOrgName?: boolean;\n    }\n    const executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const backofficeUseCase = container.resolve<IBackofficeUseCase>(IBackofficeUseCase);\n    const router = container.resolve<IRouter>(IRouter);\n    const props = defineProps<IApplicationListProps>();\n    const apps = props.apps;\n    const searchApp = ref(\"\");\n\n    const currentPage = ref(1);\n    const paginatedData = ref<Array<IApplicationData>>([]);\n    const itemsPerPageOptions = ref(['10', '20', '30', 'All']);\n    const selectedItemsPerPage = ref('10');\n\n    onBeforeMount(() => {\n        if (props.displayOrgName) getAppsOrganizationNames();\n    }) \n    function viewApp(userId: string){\n        // router.push(`/backoffice/user/${userId}`);\n    }\n    function getAppList() {\n        return apps.filter((app) => (app.name?.toUpperCase().includes(searchApp.value.toUpperCase()) || \n                                              app.createdBy?.toUpperCase().includes(searchApp.value.toUpperCase())) )\n    }\n\n    const totalPages = computed(() => {\n        if(selectedItemsPerPage.value != 'All') {\n            return Math.ceil(getAppList().length / parseInt(selectedItemsPerPage.value))\n        }\n        else {\n            return 1;\n        }\n    });\n    function getAppsOrganizationNames() {\n        executor.execute(async () => {\n            apps.forEach(async app => {\n                app.organizationName = await backofficeUseCase.getApplicationsOrganizationName(app.organizationId!)\n            })\n        },{loading: true})\n    }\n    const displayedPageNumbers = computed(() => {\n        const range = 2; // Number of page numbers to display on each side of the current page\n        let startPage = Math.max(1, currentPage.value - range);\n        let endPage = Math.min(totalPages.value, currentPage.value + range);\n        if (endPage - startPage < 2 * range) {\n            // Adjust page numbers to always show the same number of items\n            if (startPage === 1) {\n                endPage = Math.min(endPage + (2 * range - (endPage - startPage)), totalPages.value);\n            } \n            else if (endPage === totalPages.value) {\n                startPage = Math.max(startPage - (2 * range - (endPage - startPage)), 1);\n            }\n        }\n        return Array.from({ length: endPage - startPage + 1 }, (_, index) => startPage + index);\n    });\n\n    watchEffect(() => {\n        if(selectedItemsPerPage.value != 'All') {\n            const startIndex = (currentPage.value - 1) * parseInt(selectedItemsPerPage.value);\n            const endIndex = startIndex + parseInt(selectedItemsPerPage.value);\n            const newData = getAppList().slice(startIndex, endIndex);\n            paginatedData.value = newData;\n        }\n        else {\n            paginatedData.value = getAppList();\n        }\n    });\n\n    function onItemsPerPageSelect (itemCount: string) {\n        selectedItemsPerPage.value = itemCount;\n        setCurrentPage(1)\n    }\n    function setCurrentPage(page: number) {\n        currentPage.value = page;\n    }\n\n    watchEffect(() => {\n        currentPage.value = 1;\n    });\n    function formatDate(date: Date) {\n        const formatter = new Intl.DateTimeFormat('en-GB', { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric' });\n        const formattedDate = formatter.format(new Date(date));\n        return formattedDate;\n    }\n</script>\n<template>\n    <div class=\"page-title\">\n        <span>Application List ({{ apps.length }})</span>\n    </div>\n    <div class=\"filter-area\">\n        <div class=\"search-in-table\">\n            <i class=\"mdi mdi-magnify\"></i>\n            <input class=\"search-input\" type=\"text\" placeholder=\"Search\" alt=\"search\" v-model=\"searchApp\" @input=\"setCurrentPage(1)\"/>\n        </div>\n    </div>\n    <div style=\"padding-bottom: 50px;\">\n        <table class=\"apps-table\">\n            <tr class=\"thead\">\n                <th :class=\"{'col-xl-2': !props.displayOrgName, 'col-xl-1': props.displayOrgName }\">Name</th>\n                <th class=\"col-xl-1\" v-if=\"props.displayOrgName\">Organization</th>\n                <th class=\"col-xl-2\">Date Created</th>\n                <th class=\"col-xl-1\">Created By</th>\n                <th class=\"col-xl-2\">Date Updated</th>\n                <th class=\"col-xl-2\">Date Modified</th>\n                <th class=\"col-xl-1\">Date First Published</th>\n                <th class=\"col-xl-1\">Date Last Published</th>\n                <th class=\"col-xl-1\"></th>\n            </tr>\n            <tr v-for=\"app in paginatedData\" :key=\"app.ID\">\n                <!-- <td>{{ app.ID }}</td> -->\n                <td>{{ app.name }}</td>\n                    <!-- <td>{{ app.ownerUser }}</td> -->\n                    <td v-if=\"props.displayOrgName\">{{ app.organizationName }}</td>\n                    <td>{{ formatDate(app.createDate) }}</td>\n                    <td>{{ app.createdBy }}</td>\n                    <td>{{ app.updateDate ? formatDate(app.updateDate) : '-' }}</td>\n                    <td>{{ app.modifyDate ? formatDate(app.modifyDate) : '-' }}</td>\n                    <td>{{ app.firstPublishDate ? formatDate(app.firstPublishDate) : '-' }}</td>\n                    <td>{{ app.lastPublishDate ? formatDate(app.lastPublishDate) : '-' }}</td>\n                    <td>\n                        <!-- <button class=\"view-btn\">VIEW</button> -->\n                    </td>\n            </tr>\n            <!-- Pagination -->\n            <tr>\n                <td colspan=\"2\">\n                    <div style=\"display: flex; align-items: center;\">\n                        <span style=\"font-size: 14px; color: #616161\">Items per page:</span>\n                        <CustomSelect :options=\"itemsPerPageOptions\" @input=\"onItemsPerPageSelect\" class=\"items-per-page\" :selected-class=\"'item-count'\" :default=\"selectedItemsPerPage\"></CustomSelect>\n                    </div>\n                </td>\n                <td colspan=\"4\" style=\"text-align: center; font-weight: 500;\">\n                    <span v-if=\"searchApp.length > 0\">{{ getAppList().length }} items found</span>\n                </td>\n                <td colspan=\"2\">\n                    <div style=\"float: right; display: flex; align-items: center;\">\n                        <button class=\"change-page-number\" @click=\"currentPage--\" :disabled=\"currentPage === 1\">\n                            <i class=\"mdi mdi-chevron-left\" :style=\"{color: currentPage === 1 ? '#BCBCBC' : ''}\"></i>\n                        </button>\n                        <button v-for=\"pageNumber in displayedPageNumbers\" :key=\"pageNumber\" @click=\"setCurrentPage(pageNumber)\" :class=\"currentPage === pageNumber ? 'page-number active' : 'page-number'\"> {{ pageNumber }} </button>\n                        <button class=\"change-page-number\" @click=\"currentPage++\" :disabled=\"currentPage === totalPages\">\n                            <i class=\"mdi mdi-chevron-right\" :style=\"{color: currentPage === totalPages ? '#BCBCBC' : ''}\"></i>\n                        </button>\n                    </div>\n                </td>\n            </tr>\n        </table>\n\n    </div>\n</template>\n<style scoped>\n* {\n    font-family: \"Roboto\";\n}\n.page-title {\n    font-weight: 500;\n    font-size: 24px;\n    line-height: 28px;\n    color: #151233;\n}\n.filter-area {\n    margin: 20px 0;\n    display: flex;\n    align-items: center;\n}\n.search-in-table {\n    background: transparent;\n    border: 1px solid #BCBCBC !important;\n    border-radius: 4px !important;\n    height: 32px;\n    display: flex;\n    align-items: center;\n}\n.search-input {\n    background: transparent;\n    border: none;\n}\n.search-input:focus {\n    outline: none;\n}\n.search-in-table i {\n    color: #BCBCBC;\n    font-size: 20px;\n    padding: 0px 4px;\n}\n\n.view-btn {\n    background: none;\n    border:none;\n    color: #2C5A86;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n.apps-table {\n    background: #FFFFFF;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n    border-radius: 10px;\n    width: 100%;\n}\ntd:hover {\n    background-color: none !important;\n}\ntable tr:hover:not(:last-child):not(:first-child) {\n    background-color: #EEEEEE;\n}\n.apps-table th\n    {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 18px 36px\n}\n.apps-table td {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 20px 36px;\n    color: #212121;\n    font-size: 14px;\n    line-height: 16px;\n}\n.apps-table .thead {\n    background: #F5F5F5;\n    color: #616161;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n.items-per-page {\n    background: #FFFFFF;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    width: 84px;\n    padding: 7px 12px;\n    margin-left: 12px\n}\n.filters {\n    width: 115px;\n    background: transparent;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    padding: 7px 12px;\n    margin-left: 20px;\n}\n.page-number {\n    padding: 8px 12px;\n    color: #616161;\n    font-size: 14px;\n    line-height: 16px;\n    background: none;\n    border: none\n}\n.page-number.active {\n    background: #151233;\n    border-radius: 4px;\n    color: #FFFFFF;\n}\n.change-page-number {\n    border: none;\n    background: none;\n}\n.change-page-number i {\n    color: #151233; \n    font-size: 20px; \n    line-height: 20px;\n}\n\n</style>\n<style>\n    .item-count {\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .items-per-page .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n        margin-top: 8px;\n    }\n    .items-per-page .items .option {\n        color: #212121;\n        padding-left: 12px;\n        line-height: 32px;\n    }\n    .items-per-page .items .option:hover {\n        background-color: #EEEEEE;\n    }\n    .items-per-page i{\n        font-size: 16px;\n        color: #BCBCBC;\n    }\n</style>"],"sourceRoot":""}]);
